/**
 * Popup Layout with consistent prop naming and standardized variants
 * @module @voilajsx/uikit
 * @file src/components/layouts/popup.tsx
 */

import * as React from 'react';
import { forwardRef } from 'react';
import { cva } from 'class-variance-authority';
import { cn } from '@/lib/utils';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { Separator } from '@/components/ui/separator';
import { X, ChevronLeft, MoreVertical, Settings } from 'lucide-react';
import type { Size } from '@/types';

/**
 * Popup layout variants with semantic colors
 */
const popupVariants = cva(
  'flex flex-col border rounded-lg overflow-hidden',
  {
    variants: {
      variant: {
        default: 'bg-background border-border shadow-lg',
        muted: 'bg-muted/30 border-border/50 shadow-md',
        primary: 'bg-primary/5 border-primary/20 shadow-lg',
        black: 'bg-zinc-900 border-zinc-700 shadow-xl text-zinc-100'
      },
      size: {
        sm: 'w-72 max-h-80',      // 288px × 320px - Ultra compact
        md: 'w-80 max-h-96',      // 320px × 384px - Standard popup
        lg: 'w-96 max-h-[32rem]', // 384px × 512px - Large popup
        xl: 'w-[28rem] max-h-[36rem]', // 448px × 576px - Extra large
        full: 'min-w-72 max-w-lg max-h-[80vh]' // Responsive
      },
      sticky: {
        true: 'sticky top-0 z-50',
        false: 'relative'
      }
    },
    defaultVariants: {
      variant: 'default',
      size: 'md',
      sticky: false
    }
  }
);

/**
 * Header variants based on popup variant
 */
const headerVariants = cva(
  'flex items-center gap-3',
  {
    variants: {
      variant: {
        default: '',
        muted: '',
        primary: '',
        black: ''
      },
      size: {
        sm: 'p-2',
        md: 'p-3',
        lg: 'p-4',
        xl: 'p-4',
        full: 'p-4'
      }
    },
    defaultVariants: {
      variant: 'default',
      size: 'md'
    }
  }
);

/**
 * Content variants based on popup variant and size
 */
const contentVariants = cva(
  'flex-1',
  {
    variants: {
      variant: {
        default: '',
        muted: '',
        primary: '',
        black: ''
      },
      size: {
        sm: 'p-2',
        md: 'p-3',
        lg: 'p-4',
        xl: 'p-4',
        full: 'p-4'
      },
      scrollable: {
        true: 'overflow-y-auto overflow-x-hidden',
        false: ''
      }
    },
    defaultVariants: {
      variant: 'default',
      size: 'md',
      scrollable: true
    }
  }
);

/**
 * Footer variants
 */
const footerVariants = cva(
  'border-t',
  {
    variants: {
      variant: {
        default: 'border-border',
        muted: 'border-border/50',
        primary: 'border-primary/20',
        black: 'border-zinc-700'
      },
      size: {
        sm: 'p-2',
        md: 'p-3',
        lg: 'p-4',
        xl: 'p-4',
        full: 'p-4'
      }
    },
    defaultVariants: {
      variant: 'default',
      size: 'md'
    }
  }
);

/**
 * Button variants for popup actions
 */
const buttonVariants = cva(
  'h-8 w-8 p-0 shrink-0',
  {
    variants: {
      variant: {
        default: 'text-muted-foreground hover:text-foreground hover:bg-muted',
        muted: 'text-muted-foreground hover:text-foreground hover:bg-background/50',
        primary: 'text-primary/80 hover:text-primary hover:bg-primary/10',
        black: 'text-zinc-400 hover:text-zinc-100 hover:bg-zinc-800'
      }
    },
    defaultVariants: {
      variant: 'default'
    }
  }
);

/**
 * Title variants based on size
 */
const titleVariants = cva(
  'font-semibold truncate',
  {
    variants: {
      size: {
        sm: 'text-xs',
        md: 'text-sm',
        lg: 'text-base',
        xl: 'text-base',
        full: 'text-base'
      },
      variant: {
        default: 'text-foreground',
        muted: 'text-foreground',
        primary: 'text-foreground',
        black: 'text-zinc-100'
      }
    },
    defaultVariants: {
      size: 'md',
      variant: 'default'
    }
  }
);

/**
 * Subtitle variants
 */
const subtitleVariants = cva(
  'truncate mt-0.5',
  {
    variants: {
      size: {
        sm: 'text-xs',
        md: 'text-xs',
        lg: 'text-sm',
        xl: 'text-sm',
        full: 'text-sm'
      },
      variant: {
        default: 'text-muted-foreground',
        muted: 'text-muted-foreground',
        primary: 'text-muted-foreground',
        black: 'text-zinc-400'
      }
    },
    defaultVariants: {
      size: 'md',
      variant: 'default'
    }
  }
);

/**
 * Popup Layout props
 */
export interface PopupLayoutProps {
  /** Popup style variant */
  variant?: 'default' | 'muted' | 'primary' | 'black';
  /** Popup size */
  size?: Size;
  /** Whether popup header should be sticky */
  sticky?: boolean;
  /** Header title text */
  title?: string;
  /** Header subtitle text */
  subtitle?: string;
  /** Logo/icon component */
  logo?: React.ReactNode;
  /** Status badge */
  badge?: React.ReactNode;
  /** Header action buttons */
  headerActions?: React.ReactNode;
  /** Show back button */
  showBack?: boolean;
  /** Show close button */
  showClose?: boolean;
  /** Show header divider */
  showDivider?: boolean;
  /** Back button handler */
  onBack?: () => void;
  /** Close button handler */
  onClose?: () => void;
  /** Footer content */
  footer?: React.ReactNode;
  /** Enable content scrolling */
  scrollable?: boolean;
  /** Additional CSS classes */
  className?: string;
  /** Inline styles */
  style?: React.CSSProperties;
  /** Main content */
  children: React.ReactNode;
}

/**
 * PopupLayout - Compact layout with standardized props
 */
const PopupLayoutComponent = forwardRef<HTMLDivElement, PopupLayoutProps>(({
  variant = 'default',
  size = 'md',
  sticky = false,
  title,
  subtitle,
  logo,
  badge,
  headerActions,
  showBack = false,
  showClose = false,
  showDivider = true,
  onBack,
  onClose,
  footer,
  scrollable = true,
  className,
  style,
  children,
}, ref) => {

  const hasHeader = title || subtitle || logo || badge || headerActions || showBack || showClose;

  return (
    <div
      ref={ref}
      className={cn(popupVariants({ variant, size, sticky }), className)}
      style={style}
    >
      {/* Header */}
      {hasHeader && (
        <>
          <div className={cn(headerVariants({ variant, size }))}>
            {/* Back Button */}
            {showBack && (
              <Button
                variant="ghost"
                className={cn(buttonVariants({ variant }))}
                onClick={onBack}
                aria-label="Go back"
              >
                <ChevronLeft className="h-4 w-4" />
              </Button>
            )}

            {/* Logo */}
            {logo && (
              <div className="shrink-0">
                {logo}
              </div>
            )}

            {/* Title Section */}
            <div className="flex-1 min-w-0">
              {title && (
                <div className="flex items-center gap-2">
                  <h1 className={cn(titleVariants({ size, variant }))}>
                    {title}
                  </h1>
                  {badge && badge}
                </div>
              )}
              {subtitle && (
                <p className={cn(subtitleVariants({ size, variant }))}>
                  {subtitle}
                </p>
              )}
            </div>

            {/* Header Actions */}
            {headerActions && (
              <div className="shrink-0 flex items-center gap-1">
                {headerActions}
              </div>
            )}

            {/* Close Button */}
            {showClose && (
              <Button
                variant="ghost"
                className={cn(buttonVariants({ variant }))}
                onClick={onClose}
                aria-label="Close"
              >
                <X className="h-4 w-4" />
              </Button>
            )}
          </div>

          {showDivider && <Separator className={cn(
            variant === 'muted' && 'bg-border/50',
            variant === 'primary' && 'bg-primary/20',
            variant === 'black' && 'bg-zinc-700'
          )} />}
        </>
      )}

      {/* Content */}
      <div
        className={cn(
          contentVariants({ variant, size, scrollable }),
          !hasHeader && headerVariants({ variant, size })
        )}
      >
        {children}
      </div>

      {/* Footer */}
      {footer && (
        <>
          <Separator className={cn(
            variant === 'muted' && 'bg-border/50',
            variant === 'primary' && 'bg-primary/20',
            variant === 'black' && 'bg-zinc-700'
          )} />
          <div className={cn(footerVariants({ variant, size }))}>
            {footer}
          </div>
        </>
      )}
    </div>
  );
});

PopupLayoutComponent.displayName = 'PopupLayout';

/**
 * PopupHeader - Standalone header component
 */
export interface PopupHeaderProps {
  variant?: 'default' | 'muted' | 'primary' | 'black';
  size?: Size;
  title?: string;
  subtitle?: string;
  logo?: React.ReactNode;
  badge?: React.ReactNode;
  actions?: React.ReactNode;
  showBack?: boolean;
  showClose?: boolean;
  onBack?: () => void;
  onClose?: () => void;
  className?: string;
}

const PopupHeader = forwardRef<HTMLDivElement, PopupHeaderProps>(({
  variant = 'default',
  size = 'md',
  title,
  subtitle,
  logo,
  badge,
  actions,
  showBack = false,
  showClose = false,
  onBack,
  onClose,
  className,
}, ref) => {
  return (
    <div ref={ref} className={cn(headerVariants({ variant, size }), className)}>
      {showBack && (
        <Button variant="ghost" className={cn(buttonVariants({ variant }))} onClick={onBack}>
          <ChevronLeft className="h-4 w-4" />
        </Button>
      )}

      {logo && <div className="shrink-0">{logo}</div>}

      <div className="flex-1 min-w-0">
        {title && (
          <div className="flex items-center gap-2">
            <h1 className={cn(titleVariants({ size, variant }))}>{title}</h1>
            {badge && badge}
          </div>
        )}
        {subtitle && (
          <p className={cn(subtitleVariants({ size, variant }))}>{subtitle}</p>
        )}
      </div>

      {actions && <div className="shrink-0">{actions}</div>}

      {showClose && (
        <Button variant="ghost" className={cn(buttonVariants({ variant }))} onClick={onClose}>
          <X className="h-4 w-4" />
        </Button>
      )}
    </div>
  );
});

PopupHeader.displayName = 'PopupHeader';

/**
 * PopupContent - Scrollable content container
 */
export interface PopupContentProps {
  variant?: 'default' | 'muted' | 'primary' | 'black';
  size?: Size;
  scrollable?: boolean;
  className?: string;
  children: React.ReactNode;
}

const PopupContent = forwardRef<HTMLDivElement, PopupContentProps>(({
  variant = 'default',
  size = 'md',
  scrollable = true,
  className,
  children,
}, ref) => {
  return (
    <div
      ref={ref}
      className={cn(contentVariants({ variant, size, scrollable }), className)}
    >
      {children}
    </div>
  );
});

PopupContent.displayName = 'PopupContent';

/**
 * PopupFooter - Footer actions container
 */
export interface PopupFooterProps {
  variant?: 'default' | 'muted' | 'primary' | 'black';
  size?: Size;
  actions?: React.ReactNode;
  className?: string;
  children?: React.ReactNode;
}

const PopupFooter = forwardRef<HTMLDivElement, PopupFooterProps>(({
  variant = 'default',
  size = 'md',
  actions,
  className,
  children,
}, ref) => {
  return (
    <div ref={ref} className={cn(footerVariants({ variant, size }), className)}>
      {actions || children}
    </div>
  );
});

PopupFooter.displayName = 'PopupFooter';

/**
 * PopupLayout with compound components
 */
const PopupLayout = Object.assign(PopupLayoutComponent, {
  Header: PopupHeader,
  Content: PopupContent,
  Footer: PopupFooter,
});

export { 
  PopupLayout,
  PopupHeader,
  PopupContent,
  PopupFooter
};